import React from 'react';
import { Wrapper, Text, Button, ButtonWrapper } from './styles';
import { Link } from 'react-router-dom';
import ReactGA from 'react-ga';

const Main: React.FC = () => {
    const onEvent = (value: any) => {
        ReactGA.event({
            category: 'Viedo',
            action: 'View Video Page',
            value,
        });
    };

    return (
        <Wrapper>
            <Text>Google GA</Text>
            <ButtonWrapper>
                <Button onClick={() => onEvent('1')}>
                    <Link to="/first">VIDEO 1</Link>
                </Button>
                <Button onClick={() => onEvent('2')}>
                    <Link to="/second">VIDEO 2 </Link>
                </Button>
            </ButtonWrapper>
        </Wrapper>
    );
};

export default Main;
